<script>
import axios from "axios";
import { mapActions } from 'vuex'
const clientId = "3UpoleTgLO4RWnFSsh8p";
const clientSecret = "JmDFSMzMSF";



export default {
  data(){
    return {
      name: null,
    };
  },
  methods : {
    ...mapActions(['updateUser','initializeSections']),
    async getInfo(){
      try {
        console.log(33)
        const url = `/v1/nid/me`;
        let header = "Bearer " + this.accessToken;
        const headers = { Authorization: header };
        const { data } = await axios.get(url, { headers });
        console.log("userInfo: ", data);

        const userInfo=data.response.birthyear+data.response.name
        this.$store.dispatch('updateUser', userInfo);

        this.setCookie("name", userInfo)        //           

        window.location.href = "/new.io";
      } catch (error) {
        console.error("   :", error);
        window.location.href = "/new.io";
      }
    },
    setCookie(name, value, days) {
    //const date = new Date();
    //date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
    document.cookie = `${name}=${value};path=/`;
    },
  },
    mounted() {
    this.naverLogin = new window.naver_id_login(clientId);
    this.accessToken = this.naverLogin.oauthParams.access_token;
    console.log(this.accessToken)
    this.getInfo();
  },

}

</script>